<template>
  <layout-default>
    <div class="page-content">
        <div class="container-fluid">

            <!-- start page title -->
            <div class="row">
                <div class="col-12">
                    <div class="page-title-box d-flex align-items-center justify-content-between">
                        <h4 class="mb-0">
                        <router-link  v-if="order_payment" :to="{ name: 'client_orders_show', params: { order_id: order_payment.order.id } }" tag="a" >
                                <i class="fas fa-arrow-left fa-x"></i>
                        </router-link>
                        تفاصيل القسط</h4>
                    </div>
                </div>
            </div>
            <!-- end page title -->

            <div class="row">
                <div class="col-lg-12" v-if="order_payment">
                    <div class="card" dir="rtl">
                        <div class="card-body" style="margin: 20px;">
                            <div class="invoice-title">
                                <h4 class="font-size-16" style="text-align:left;"><b>رقم الفاتورة</b> #{{order_payment.order.id}}{{order_payment.order.created_at| moment("DDMMYY")}}</h4>
                                <hr class="my-4">
                                <div class="py-2">
                                    <h5 class="font-size-20 col-md-8" style="font-weight:600;">تفاصيل عامة</h5>
                                </div>
                                <div class="row">
                                    <div class="col-md-3 col-sm-4 col-xs-6 mt-2">
                                        <b>أسم العميل : </b>&nbsp;{{order_payment.order.name}}
                                    </div>
                                    <div class="col-md-3 col-sm-4 col-xs-6 mt-2">
                                        <b>تاريخ الأستحقاق : </b>&nbsp;{{order_payment.pay_at| moment("DD-MM-YYYY")}}
                                    </div>
                                    <div class="col-md-3 col-sm-4 col-xs-6 mt-2">
                                        <b>مبلغ القسط : </b>&nbsp;{{moneyFormat(order_payment.money,0)}} L.E
                                    </div>
                                    <div class="col-md-3 col-sm-4 col-xs-6 mt-2">
                                        <b>باقي القسط : </b>&nbsp;{{moneyFormat(order_payment.amount,0)}} L.E
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="card" dir="rtl" v-for="(payment, index) in order_payment.client_payments">
                        <div class="card-body" style="margin: 20px;">
                            <div class="invoice-title">
                                <div class="table-responsive hidden-print" dir="ltr">
                                    <table class="table table-nowrap table-centered mb-0">
                                        <thead>
                                            <tr>
                                                <th>المبلغ المسدد</th>
                                                <th>أسم المحصل</th>
                                                <th>تاريخ السداد</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>{{moneyFormat(payment.money,0)}} L.E</td>
                                                <td>{{payment.user.name}}</td>
                                                <td>{{payment.created_at| moment("DD-MM-YYYY h:mm A")}}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                    <iframe 
                                        v-if="payment.lat&&payment.lng"
                                        width="100%" 
                                        height="300" 
                                        frameborder="0" 
                                        scrolling="no" 
                                        marginheight="0" 
                                        marginwidth="0" 
                                        :src="'https://maps.google.com/maps?q='+payment.lat+','+payment.lng+'&hl=es&z=16&amp;output=embed'"
                                        >
                                    </iframe>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- end row -->
            
        </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import axios from 'axios';


export default {
  components: {LayoutDefault,PaginationNav},
  data() {
        return {
            order_payment:null
        }
    },  
  created() { 
    this.fetchData();
  },
  methods: {
    fetchData() {
      axios.get('/api/order_payments/'+this.$route.params.payment_id).then(({data}) => {
          this.order_payment=data.success.order_payment;
      })
      //.catch((error) => {this.$router.push({ name: "home"})});  
    },
  },
}
</script>
<style scoped>
    @media print {
        .hidden-print {
            display: none!important;
        }
    }
</style>